/* #region top-bar */
.top-bar {
  padding-block: 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  @include on-tablet {
    padding-block: 36px;
  }

  &__icons {
    display: flex;
    align-items: center;
    gap: 24px;
  }

  &__logo-link {
    display: flex;
  }

  &__logo {
    height: 38px;

    @include hover(transform, scale(1.2));
  }
}

/* #endregion */
